import React from 'react';

import home from '../assets/images/screenshots/iphone_6_plus_home.png';
import edit from '../assets/images/screenshots/iphone_6_plus_edit.png';
import settings from '../assets/images/screenshots/iphone_6_plus_settings.png';

const screens = {
  home,
  edit,
  settings,
};

const Screenshot = ({ device, orientation, color, screen }) => (
  <div className="device-container">
    <div className={ `device-mockup ${ device } ${ orientation } ${ color }` }>
      <div className="device">
        <div className="screen">
          <img src={ screens[screen] } className="img-fluid" alt="Screenshot of Ticker on an iPhone" /> 
        </div>
        <div className="button"></div>
      </div>
    </div>
  </div>
);

Screenshot.defaultProps = {
  device: 'iphone6_plus',
  orientation: 'portrait',
  color: 'white',
  screen: 'home',
}

export default Screenshot;
