import React, { useState, useEffect } from 'react';
import config from '../../config';
import Scroll from './Scroll';

export default props => {
  const [ openMenu, setOpenMenu ] = useState(false);
  const [ visibilityClass, setVisibilityClass ] = useState('');
  const [ scrollOffset, setScrollOffset ] = useState(0);

  const toggleMenu = value => setOpenMenu(value);

  const handleScroll = () => {
    setScrollOffset(window.pageYOffset);
  }

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return function () {
      window.removeEventListener('scroll', handleScroll);
    }
  }, []);

  useEffect(() => {
    const updatedClass = scrollOffset > 300 ? 'navbar-shrink' :  '';
    if (updatedClass !== visibilityClass) {
      setVisibilityClass(updatedClass);
    }
  }, [ scrollOffset, visibilityClass ]);

  return (
    <nav
      className={`navbar navbar-expand-lg navbar-light fixed-top ${visibilityClass} ${ props.nohero ? 'navbar--no-hero' : ''}`}
      id="mainNav"
    >
      <div className="container">
        <a className="navbar-brand" href="#page-top">
          {config.siteTitle}
        </a>
        <button
          onClick={_ => toggleMenu(!openMenu)}
          className={`navbar-toggler navbar-toggler-right ${
            openMenu ? '' : 'collapsed'
          }`}
          type="button"
          aria-controls="navbarResponsive"
          aria-expanded={openMenu}
          aria-label="Toggle navigation"
        >
          Menu
          <i className="fas fa-bars"></i>
        </button>

        <div
          className={`collapse navbar-collapse ${openMenu ? 'show' : ''}`}
          id="navbarResponsive"
        >
          <ul className="navbar-nav ml-auto">
            <li className="nav-item">
              <Scroll
                onClick={_ => toggleMenu(!openMenu)}
                type="id"
                element="download"
              >
                <a className="nav-link" href="#download">
                  Download
                </a>
              </Scroll>
            </li>
            <li className="nav-item">
              <Scroll
                onClick={_ => toggleMenu(!openMenu)}
                type="id"
                element="features"
              >
                <a className="nav-link" href="#features">
                  Features
                </a>
              </Scroll>
            </li>
            <li className="nav-item">
              <Scroll
                onClick={_ => toggleMenu(!openMenu)}
                type="id"
                element="contact"
              >
                <a className="nav-link" href="#contact">
                  Contact
                </a>
              </Scroll>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}
